
.widgets{
    width: 100%;
    display: flex;
    .board{
        width: 50%;
        margin: 5px;
        padding: 10px;
        border: 2px dashed transparent;
        &.hovered { border: 2px dashed #d5d5d5; }
    }
    .widget{
        padding: 0px;
        width: 100%;
        background: #fff;
        border-radius: 4px;
        animation: show 1s;
        pointer-events: auto;
        .header{
            cursor: move;
            background: $primary;
            color: #fff;
            display: flex;
            height: 60px;
            padding: 20px;
            justify-content: space-between;
            .title{ margin: 0px; }
        }
        .header > .action{
            display: flex;
            .icon{
                width: 20px;
                height: 20px;
                cursor: pointer;
                margin-left: 5px;
                display: flex;
                & > * {
                    width: 20px;
                    height: 20px;
                }
            }
        }
        .header:active{ cursor: grabbing; }
        .body{ 
            padding: 10px 20px;
            .chart{
                .chart-title{
                    font-size: 12px;
                    margin-bottom: 10px;
                    text-align: center;
                    display: block;
                }
                .chart-content table thead th{ width: calc(100% / 8) !important; }
            }
        }
    }
    .tile{
        &::before{
            content: '';
            height: 200px;
            width: 100%;
            display: none;
            margin-bottom: 20px;
            border: 2px dashed #ddd;
            border-radius: 5px;
        }
        &:last-child .card{ margin-bottom: 0px; }
        &.hovered::before{ display: block; }
    }
}

@media (max-width: 991.98px) {
    .widgets{
        flex-direction: column;
        .board{
            width: 100%;
        }
    }
}


@media (max-width: 991.98px) { 
    .detail .specs {
        .label, .value{ width: 50%; }
    }
}